
.item{ list-style: none; margin: 10px; border-bottom: 1px solid #eaeaea; padding: 10px 0;}
.item div{ display: flex; gap: 0 10px; justify-content: space-between; align-items: center; }
.item label {  flex-grow: 1; cursor: pointer; }
.item input{ margin-right: 20px; }
.item button {
  font-size: 13px;
  background: steelblue;
  border: none;
  cursor: pointer;
  color: white;
  outline: none;
  border-radius: 100px;
  height: 50px;
  width: 50px;
}