*{ padding: 0; margin: 0; box-sizing: border-box;}
html{-webkit-tap-highlight-color: transparent;}
body{ font-family: "Segoe UI", Arial, sans-serif; line-height: 1.5; background: #f1f1f1; height: 100vh;}
.container{ display: flex; justify-content: center; align-items: center; max-width: 600px;}
.inner{ width: 100%; padding: 20px; }
.form-container{ width: 100%; display: flex; border-radius: calc(0.5 * 100px);box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38); justify-content: space-evenly;}
.input-text{ font-size: 1rem; font-weight: 400;  width: 85%;  padding-right: 5px;border-radius: calc(0.5 * 100px);  padding-left: 10px; }
.input-text::placeholder {color: #000;}
.input-submit { background: transparent; color: #5b5b5b; text-transform: capitalize; cursor: pointer; font-weight: 600; margin-right: 10px;}
.input-text,.input-submit { height: 45px; outline: none; border: none;}
.error{ margin-top: 20px; color: red; }
/*nav a { text-decoration: none; }*/
nav a.active{ color: orangered; }
.navBar {
  position: relative;
}

.navBar > button {
  position: fixed;
  left: 40px;
  top: 40px;
  z-index: 10;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
}

.menuNav {
  overflow-y: scroll;
  list-style: none;
  position: fixed;
  top: 0;
  background: darkcyan;
  left: 0;
  bottom: 0;
  height: 100vh;
  width: 0;
  overflow: hidden;
  max-width: 300px;
  z-index: 9;
  transition: width 1s ease;
}

.menuNav.showMenu {
  width: 100%;

}
.menuNav li:first-child {
  margin-top: 7rem;
}


a {
  display: block;
  padding: 10px 40px;
  text-decoration: none;
  color: #3fffd2;
  text-transform: uppercase;
  font-weight: bold;
}


.about-container{ padding-top: 100px; }